@import url("https://fonts.googleapis.com/css2?family=Dosis&family=Playfair+Display:wght@400;700;900&family=Roboto:wght@300;400;500;900&display=swap&family=Hind&display=swap");
h1, h2, h3, h4, h5, h6 {
  font-family: "Playfair Display", serif;
  font-weight: 400;
  color: #222;
  letter-spacing: 0;
  text-transform: none;
}

#newWork #projects .project .overlay .details h5 {
  font-family: "Hind", sans-serif;
}

*, *:after, *:before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scrollbar-color: #000 #fff !important;
  scrollbar-width: thin !important;
}

body {
  height: 100vh;
  overflow: auto;
}

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {
  background: #fff;
}

::-webkit-scrollbar-thumb {
  background: #000;
}

h1 {
  font-size: 60px;
  line-height: 82px;
}

h2 {
  font-size: 44px;
  line-height: 60px;
}

h3 {
  font-size: 40px;
  line-height: 60px;
}
@media screen and (max-width: 600px) {
  h3 {
    font-size: 28px;
    line-height: 52px;
  }
}

h4 {
  font-size: 32px;
  line-height: 50px;
}

h5 {
  font-size: 22px;
  line-height: 40px;
}

h6 {
  font-size: 13px;
  line-height: 28px;
}

p {
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  font-weight: 300;
  color: #606060;
  letter-spacing: 0;
  line-height: 30px;
}

a.desktop {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 14px;
  color: #232323;
  text-transform: uppercase;
  text-decoration: none;
  line-height: 51px;
  letter-spacing: 2px;
  transition: all 0.5s ease;
  position: relative;
}
a.desktop:after {
  position: absolute;
  top: 45px;
  left: 0;
  height: 2px;
  width: 20px;
  display: inline-block;
  text-align: center;
  left: 50%;
  transform: translateX(-50%) scale(0, 1);
  z-index: 1;
  content: "";
  background: #41e2ad;
  transition: transform 0.5s ease;
}
a.desktop.active {
  color: #41e2ad;
}
a.desktop:hover {
  color: #41e2ad;
}
a.desktop:hover:after {
  transform: translateX(-50%) scale(1, 1);
}

a.mobile {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 14px;
  color: #232323;
  text-transform: uppercase;
  text-decoration: none;
  line-height: 51px;
  letter-spacing: 2px;
  transition: all 0.5s ease;
}
a.mobile.active {
  color: #41e2ad;
}
a.mobile:hover {
  color: #41e2ad;
}

html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
}

main {
  display: block;
}

h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: transparent;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

.about__container {
  display: flex;
  justify-content: center;
  overflow: hidden;
}
.about__container.contact {
  min-height: calc(100vh - 120px);
}
@media screen and (max-width: 768px) {
  .about__container.contact {
    flex-direction: column-reverse;
  }
}
@media screen and (max-width: 768px) {
  .about__container {
    flex-direction: column;
  }
}
.about__container .about__image {
  flex: 1;
}
@media screen and (max-width: 768px) {
  .about__container .about__image {
    height: 365px !important;
    flex: 0;
    overflow: hidden;
  }
}
.about__container .about__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.about__container .about__image img.noObj {
  object-position: 50%;
}
@media screen and (max-width: 768px) {
  .about__container .about__image img {
    height: 365px;
  }
}
.about__container .about__me {
  flex: 1;
  padding: 150px;
}
@media screen and (max-width: 1440px) {
  .about__container .about__me {
    padding: 100px;
  }
}
@media screen and (max-width: 1024px) {
  .about__container .about__me {
    padding: 60px;
  }
}
@media screen and (max-width: 425px) {
  .about__container .about__me {
    padding: 30px;
  }
}
.about__container .about__me h1 {
  margin-bottom: 30px;
}
@media screen and (max-width: 1024px) {
  .about__container .about__me h1 {
    font-size: 44px;
  }
}
@media screen and (max-width: 768px) {
  .about__container .about__me h1 {
    font-size: 52px;
  }
}
@media screen and (max-width: 425px) {
  .about__container .about__me h1 {
    font-size: 44px;
  }
}
.about__container .about__me h5 {
  margin-bottom: 30px;
