}
.about__container .about__me .skills code.php {
  background: #858EB8;
}
.about__container .about__me .skills code.mysql {
  background: #4479A1;
}
.about__container .about__me .skills code.vs {
  background: #0078D7;
}
.about__container .about__me .skills code.boot {
  background: #7709F6;
}
.about__container .about__me .skills code.found {
  background: #074E68;
}
.about__container .about__me .skills code.bluma {
