}
.about__container .about__me .skills code.tailwind {
  background: #06B0CD;
}
.about__container .about__me .skills code.blender {
  background: #E87D0D;
}
.about__container .about__me a {
  font-family: "Roboto", sans-serif;
  display: inline-flex;
  margin-top: 50px;
  font-size: 15px;
  color: #fff;
  background: #000;
  text-decoration: none;
  padding: 18px 35px;
  border-radius: 30px;
  font-weight: 400;
  text-transform: uppercase;
  transition: all 0.5s ease;
}
.about__container .about__me a:hover {
  background: #41e2ad;
}
.about__container address {
  margin-top: 60px;
  font-style: regular;
}
.about__container address .address, .about__container address .email {
  font-family: "Dosis", sans-serif;
  font-style: normal;
  text-transform: uppercase;
  letter-spacing: 2px;
}
.about__container address p {
  font-style: normal;
  margin-top: 15px;
  margin-bottom: 30px;
}
.about__container address p.nomar {
  margin-bottom: 0;
  margin-top: 15px;
}
.about__container address p.sm {
  margin-top: 5px;
}

.footer__container {
  background: #000;
  padding: 50px 95px;
}
.footer__container p {
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 5px;
}

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 30px 40px;
  border-bottom: 1px solid #e9e9e9;
}
@media screen and (max-width: 425px) {
  .header__container {
    padding: 20px;
  }
}
.header__container .header__logo {
  text-align: center;
}
.header__container .header__logo a {
  text-decoration: none;
}
.header__container .header__logo .logo__title {
  font-family: "Playfair Display", serif;
  font-size: 26px;
  font-weight: 900 !important;
  letter-spacing: 6px;
  color: #000;
  text-transform: uppercase;
  margin-bottom: 10px;
  display: block;
}
.header__container .header__logo .logo__designation {
  font-family: "Roboto", sans-serif;
  text-transform: uppercase;
  font-size: 10px;
  font-weight: 500;
  letter-spacing: 4px;
  color: #222;
}
.header__container nav.desktop__nav {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 50px;
}
.header__container nav.desktop__nav .links {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 50px;
}
@media screen and (max-width: 768px) {
  .header__container nav.desktop__nav .links {
    display: none;
  }
}
.header__container nav.desktop__nav .burger {
  display: none;
  transition: all 0.5s ease;
  align-items: center;
  height: 30px;
}
@media screen and (max-width: 768px) {
  .header__container nav.desktop__nav .burger {
    display: flex;
  }
}
.header__container nav.desktop__nav .burger .line {
  width: 30px;
  height: 2px;
  background: #000;
  position: relative;
  transition: all 0.5s ease;
}
.header__container nav.desktop__nav .burger .line:after, .header__container nav.desktop__nav .burger .line:before {
  width: 30px;
  height: 2px;
  background: #000;
  content: "";
  position: absolute;
  transition: all 0.5s ease;
}
.header__container nav.desktop__nav .burger .line:before {
  transform: translateY(-8px);
}
.header__container nav.desktop__nav .burger .line:after {
  transform: translateY(8px);
}
.header__container nav.desktop__nav .burger.open .line {
  transform: translate(-50px);
  background: transparent;
}
.header__container nav.desktop__nav .burger.open .line:before {
  transform: rotate(45deg) translate(35px, -35px);
}
.header__container nav.desktop__nav .burger.open .line:after {
  transform: rotate(-45deg) translate(35px, 35px);
}

.header__mobile {
  height: 0;
  overflow: hidden;
  transition: all 0.5s ease;
  display: none;
}
@media screen and (max-width: 768px) {
  .header__mobile {
    display: block;
  }
}
.header__mobile.visible {
  height: 156px;
}
.header__mobile nav.mobile__nav .links {
  display: flex;
  flex-direction: column;
}
.header__mobile nav.mobile__nav .links a {
  border-bottom: 1px solid #e9e9e9;
  padding: 0 20px;
}

.banner__container {
  max-width: 1300px;
  margin: 0 auto;
  padding: 70px 95px 100px 95px;
}
@media screen and (max-width: 600px) {
  .banner__container {
    padding: 70px 50px 100px 50px;
  }
}
@media screen and (max-width: 425px) {
  .banner__container {
    padding: 70px 20px 100px 20px;
  }
}
.banner__container h3 {
  margin-bottom: 30px;
}
.banner__container .kicker {
  font-size: 20px;
  font-weight: 300;
  line-height: 30px;
  color: #606060;
}

#newWork {
  width: 100% !important;
}
#newWork #projects {
  font-size: 0;
}
#newWork #projects .project {
  width: 50%;
  width: 33.33%;
  height: 100%;
  display: inline-flex;
  position: relative;
  margin: 0;
  padding: 0;
  text-decoration: none;
  transition: 0.5s ease;
  overflow: hidden !important;
}
@media screen and (max-width: 1200px) {
  #newWork #projects .project {
    width: 50%;
  }
}
@media screen and (max-width: 768px) {
  #newWork #projects .project {
    width: 100%;
  }
}
@media (hover: none) {
  #newWork #projects .project {
    flex-direction: column;
    border-bottom: 1px solid #d7d7d7;
    margin: 5px 0;
  }
}
#newWork #projects .project img {
  height: 100% !important;
  width: 100%;
  object-fit: cover;
  transition: 2.5s ease;
  overflow: hidden;
}
#newWork #projects .project .overlay {
  position: absolute;
  top: 10px;
  right: 10px;
  bottom: 10px;
  left: 10px;
  background: rgba(0, 0, 0, 0.8);
  background: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: all 0.5s ease;
  z-index: 10;
  pointer-events: none;
}
@media (hover: none) {
  #newWork #projects .project .overlay {
    position: static;
    opacity: 1;
    top: auto;
    bottom: 0;
    right: 0;
    left: 0;
    align-items: flex-start;
    padding: 10px 20px;
    text-decoration: none;
  }
}
#newWork #projects .project .overlay .details {
  text-align: center;
}
#newWork #projects .project .overlay .details h5 {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #fff;
  color: rgba(0, 0, 0, 0.8);
  transition: 0.5s ease;
  text-transform: uppercase;
  position: relative;
  bottom: -15px;
  opacity: 0;
}
@media (hover: none) {
  #newWork #projects .project .overlay .details h5 {
    opacity: 1;
    text-align: left;
    bottom: 0;
  }
}
#newWork #projects .project .overlay .details p {
  font-family: "Hind", sans-serif;
  color: #222;
  font-weight: 400;
  transition: 0.5s ease;
  font-size: 14px;
  position: relative;
  top: -15px;
  opacity: 0;
  font-weight: normal;
}
@media (hover: none) {
  #newWork #projects .project .overlay .details p {
    opacity: 1;
    top: 0;
    text-align: left;
  }
}
#newWork #projects .project:hover .overlay {
  opacity: 1;
}
#newWork #projects .project:hover .details h5 {
  bottom: 0;
  opacity: 1;
}
#newWork #projects .project:hover .details p {
  top: 0;
  opacity: 1;
}

#project .project__container {
  display: flex;
  position: relative;
}
@media screen and (max-width: 960px) {
  #project .project__container {
    flex-direction: column;
  }
}
#project .project__container .project__content {
  width: 50%;
  height: 100%;
  padding: 50px 80px;
  position: sticky;
  top: 0;
}
@media screen and (max-width: 960px) {
  #project .project__container .project__content {
    width: 100%;
    position: static;
  }
}
@media screen and (max-width: 960px) {
  #project .project__container .project__content {
    padding: 50px 40px;
  }
}
#project .project__container .project__content h5 {
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 1px;
  line-height: 25px;
}
#project .project__container .project__content p {
  font-size: 15px;
}
#project .project__container .project__content p.project__description {
  margin-bottom: 25px;
}
#project .project__container .project__content .project__details {
  margin-bottom: 25px;
}
#project .project__container .project__content .project__details a {
  font-family: "Roboto", sans-serif;
  font-size: 13px;
  text-transform: uppercase;
  line-height: 14px;
  color: #222;
  font-weight: 500;
  text-decoration: none;
  letter-spacing: 1px;
  display: inline-flex;
  align-items: center;
  transition: 0.3s ease;
}
#project .project__container .project__content .project__details a:before {
  display: flex;
  content: "";
  width: 20px;
  height: 2px;
  background: #000;
  margin-right: 12px;
  transition: 0.5s ease;
}
#project .project__container .project__content .project__details a:hover:before {
  width: 30px;
  background: #41e2ad;
}
#project .project__container .project__images {
  width: 50%;
}
@media screen and (max-width: 960px) {
  #project .project__container .project__images {
    width: 100%;
  }
}
#project .project__container .project__images .image {
  font-size: 0;
}
#project .project__container .project__images .image img {
  width: 100%;
  height: 100%;
}